<template>
    <div class="list-container">
      <grid-space/>
    </div>
</template>

<script>
export default {
  name: 'SurveyListContainer',
  components: {
    'gridSpace': () => import('@/components/survey/list/space/GridSpace')
  }
}
</script>

<style scoped>
 div.list-container {
    padding: 10px;
    width: 100em;
    height: 100%;
    margin: auto;
 }
</style>



